/* import */
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,200;1,500;1,800&display=swap');

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

h2{
    font-weight: 500;
}

h1{
    font-weight: 800;
}

p{
    font-weight: 200;
}

/* colors */

$color-princepal : #2357a0;
/* $color-princepal : #03174cAA; */
$color-secondary : lighten($color-princepal , 10);
$color-white : #fff;
$color-black : #000;



.container{
    background-color: $color-princepal;
    height: 100vh;
    width: 100vw;
    display: grid;
    grid-template-columns: 20% 80%;
    color: $color-white;
    transition: .2s;




    @media screen and (max-width:1024px){
        grid-template-columns: 15% 85%;
    }

    @media screen and (max-width:414px){
        grid-template-columns: 0 100%;

    }


    &_nav-bar{
        background-color: $color-secondary;
        display: grid;
        position: relative;
        /* border-top-right-radius: 20px; */
        filter: drop-shadow(10px 10px 15px $color-black);
        z-index: 100;

        &_logo{
            margin: 20px auto;
            background-color: $color-princepal;
            width: 60%;
            height: 70%;
            overflow: hidden;
            border-radius: 10px;
            filter: drop-shadow(10px 10px 15px $color-black);
            cursor: pointer;
            border-top: 1px solid rgba(255 ,255 ,255 ,0.5);
            border-left: 1px solid rgba(255 ,255 ,255 ,0.5);
            
            img{
                border-radius: 10px;
                width: 100%;
                height: 100%;
            }
        }

        

        &_menu{
            display: grid;

            ul{
                margin: 0 auto;
                width: 100%;
                overflow: hidden;

                a{
                    text-decoration: none;
                    color: $color-white;
                    margin-left: 5px;
                    

                    li{
                        filter: drop-shadow(10px 10px 5px $color-black);
                        list-style: none;
                        padding: 20px;
                        width: 100%;
                        margin-left: 10px;
                        background-color: $color-princepal;
                        /* margin: 20px; */
                        border-bottom-left-radius: 20px;
                        border-top-left-radius: 20px;
                        cursor: pointer;
                        transition: .3s all;
                        border-top: 1px solid rgba(255 ,255 ,255 ,0.5);
                        border-left: 1px solid rgba(255 ,255 ,255 ,0.5);

                        i{
                            font-size: 20px;
                            list-style: none;
                        }
    
    
                    }


                }



                li:hover {
                    color: #000;
                    padding: 20px 40px;
                }
                li:hover a{
                    color: #000;
                }
            }
        }



 

        @media screen and (max-width:1024px){

            

            &_logo{
                height: 30%;
                width: 80%;
            }

            &_menu{    
                ul{    
                    align-items: center;

                    a{
                        text-decoration: none;
                        color: $color-white;
                        text-align: center;

                        li{

                            width: 80%;
                            border-radius: 20px;
                            i{    
                                display: flex;
                                justify-content: center;
                                font-size: 35px;
    
                            }
        
                        }
                    }

                }
            }


        }
    }

    &_contante{
        background-image:url(../images/IMG-0829.jpg);
        background-position:0px -65px;
        background-repeat: no-repeat;
        background-size: cover;
        position: relative;
        @media screen and (max-width:414px){
        
            background-position:0px ;
        }

        

        &_main{
            background: linear-gradient(135deg, rgba(255 ,255 ,255 ,0.1), rgba(255 ,255 ,255 ,0.1));
            backdrop-filter: blur(4px);
            border-radius: 10px;
            padding: 0 15px;
            position: absolute;
            top: 10%;
            bottom: 10%;
            left: 10%;
            right: 10%;
            filter: drop-shadow(10px 10px 10px $color-black);
            border-top: 1px solid rgba(255 ,255 ,255 ,0.5);
            border-left: 1px solid rgba(255 ,255 ,255 ,0.5);
            overflow: hidden;

            &_titel{
                text-align: center;
                /* margin-top: 5%; */
                cursor: default;

                h3{
                    font-size: 40px;
                    margin-top: 5px;
                    font-weight: 500;
                }

                h1{
                    margin: 0 5px;
                    font-size: 70px;
                    background: linear-gradient(135deg, #2357a023 , #2357a023);
                    backdrop-filter: blur(4px);
                    border-radius: 20px;
                    border-top: 1px solid rgba(255 ,255 ,255 ,0.5);
                    border-left: 1px solid rgba(255 ,255 ,255 ,0.5);
                    
                }
            }


            @media screen and (max-width:1024px){
                &_titel{
                    /* margin-top: 10%; */
    
                    h3{
                        font-size: 40px;
                        margin-top: 0px;
                        font-weight: 500;
                    }
    
                    h1{
                        margin: 0 5px;
                        text-decoration: underline;
                        font-size: 55px;
                    }
                }
            }

            @media screen and (max-width:414px){
                &_titel{
                    /* margin-top: 10%; */
    
                    h3{
                        font-size: 20px;
                        margin-top: 0px;
                        font-weight: 500;
                    }
    
                    h1{
                        margin: 0 5px;
                        text-decoration: underline;
                        font-size: 30px;
                    }
                }
            }




            &_text{
                
                ul{
                    margin-top: 2%;
                    display: grid;
                    grid-template-columns: 1fr 1fr ;
                    gap: 30px;

                    li{
                        background: linear-gradient(135deg, #2357a023 , #2357a023);
                        backdrop-filter: blur(4px);
                        padding: 20px 30px;
                        border-radius: 10px;
                        list-style: none;
                        border-top: 1px solid rgba(255 ,255 ,255 ,0.5);
                        border-left: 1px solid rgba(255 ,255 ,255 ,0.5);
                        cursor: default;

                    }

                    @media screen and (max-width:1024px){
                        grid-template-columns: 1fr;
                        gap: 10px;

                        li{
                            text-align: center;
                            padding: 10px 20px;
                        }
                    }
                }

            }

            span{
                display: flex;
                justify-content: space-around;

                h1{
                    background: linear-gradient(135deg, #275ca763 , #275ca763);
                    margin-top: 15px;
                    padding: 15px;
                    border-radius: 30px;
                    border-top: 1px solid rgba(255 ,255 ,255 ,0.5);
                    border-left: 1px solid rgba(255 ,255 ,255 ,0.5);
                    text-align: center;

                    a{
                        text-decoration: none;
                        color: $color-white;
                        font-weight: 500;
                    }
                }

                @media screen and (max-width:414px){
                    display: block;

                    h1{
                        padding: 5px;
                    }
                }

                @media screen and (max-width:1024px){

                    h1{
                        padding: 10px;
                    }
                }


            }


        }
        



        #Open{
            color: $color-white;
            font-size: 40px;
            position: absolute;
            top: 5px;
            left: 5px;
            display: none;
        }

        #Close{
            color: $color-white;
            font-size: 40px;
            position: absolute;
            top: 5px;
            right: 5px;
            display: none;
        }


        @media screen and (max-width:414px){
        
    
            #Open{
                    color: $color-white;
                    font-size: 40px;
                    position: absolute;
                    top: 5px;
                    left: 5px;
                    display: block;
                    z-index: 10000;
                }
        }

    }
}


.Active{
    grid-template-columns: 30% 70%;
}

.desactive{
    grid-template-columns: 0% 100%;
}